import React, { Component, Suspense, lazy } from 'react';
import { Link, HashRouter as Router, Route } from "react-router-dom";
import Loder from '../Loder';
import { gojsData } from "../../assets/data";
import "./index.less";

const GoJSintro = lazy(() => import('./Intro'));
const GoJSfirst = lazy(() => import('./First'));
const GoJSsecond = lazy(() => import('./Second'));
const GoJSthird = lazy(() => import('./Third'));
const GoJSforth = lazy(() => import('./Forth'));
const PageFooter = lazy(() => import('../PageFooter'));

export default class index extends Component {
    componentDidMount() {
        window.addEventListener('hashchange', () => {
            document.getElementsByClassName("header")[0].className = "ant-layout-header header header-visible";
        })
    }
    render() {
        return (
            <Router>
                <Suspense fallback={<Loder />}>
                    <Route exact path={gojsData.catalog[0].href} render={() => (<GoJSintro />)} />
                    <Route exact path={gojsData.catalog[1].href} render={() => (<GoJSfirst />)} />
                    <Route exact path={gojsData.catalog[2].href} render={() => (<GoJSsecond />)} />
                    <Route exact path={gojsData.catalog[3].href} render={() => (<GoJSthird />)} />
                    <Route exact path={gojsData.catalog[4].href} render={() => (<GoJSforth />)} />
                    <Route exact path={gojsData.baseHref} render={() => (
                        <div className="catalog">
                            <div className="container">
                                <div className="title">
                                    <h1>{gojsData.title}</h1>
                                </div>
                                <div className="contents">
                                    <ul>
                                        {gojsData.catalog.map((item, index) => {
                                            return (
                                                <li key={index}><Link to={item.href}>{item.title}</Link></li>
                                            )
                                        })}
                                    </ul>
                                </div>
                            </div>
                        </div>
                    )} />
                    <Route path="/gojs/:id" render={() => (<PageFooter />)} />
                </Suspense>
            </Router>
        )
    }
}
